import React, { useState } from 'react'
import { Configuration,OpenAIApi } from 'openai'
import Spinner from 'react-bootstrap/Spinner';

const Form = () => {

    const [result, setResult] = useState("");


    const configuration = new Configuration({
        apiKey: "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",
    });

    const openai = new OpenAIApi(configuration);

    const generateImage = async () => {
        const res = await openai.createImage({
          prompt: prompt,
          n: 1,
          size: "512x512",
        });
    
        setResult(res.data.data[0].url);
      };
    
    const [prompt,setPrompt]=useState("")
  return (
    <div className='form'>
        <div class="input-group input-group-lg">
            <div class="input-group-prepend">
            <span class="input-group-text" id="inputGroup-sizing-lg">Type something</span>
    </div>
    <input type="text"  onChange={(e)=>setPrompt(e.target.value)}  class="form-control" aria-label="Large" aria-describedby="inputGroup-sizing-sm"/>
    </div>
    <br></br>
    <button type="button" onClick={generateImage} class="btn btn-primary btn-lg">Submit</button>
    {result.length > 0 ? (
          <img className="result-image" src={result} alt="result" />
        ) : (
          <></>
        )}
    </div>
    
  )
}

export default Form
